/* @import '../../common.css'; */

.home-wrapper {
  display: flex;
  justify-content: space-between;
}

.filters-wrapper {
  display: flex;
  justify-content: start;
  flex-direction: column;
  margin-right: 20px;
}

.filters-btns {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.filter-btn {
  font-family: JetBrains Mono, monospace;
  display: block;
  height: 40px;
  background-color: var(--main-info-color);
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
  padding: 10px 30px;
  width: 200px;
}

.filter-btn:active {
  transform: scale(0.90);
}

.filter-btn:hover {
  transition: 0.5s;
  background-color: var(--main-info-color-hover);
}

#copy-link {
  background-color: var(--orange-color);
}

.filters-block {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  border: solid 1px var(--font-color);
  padding: 10px;
}

.filters-title {
  font-size: var(--main-info-font-size);
}

.filters-list {
  height: 150px;
  overflow-y: scroll;
}

.amount {
  margin-right: 10px;
}

.filter {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.filter-check {
  margin-right: 10px;
}


.range_container {
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: 35% auto;
}

.sliders_control {
  position: relative;
  min-height: 50px;
}

.form_control {
  position: relative;
  display: flex;
  justify-content: space-between;
  font-size: 24px;
  color: #635a5a;
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  pointer-events: all;
  width: 24px;
  height: 24px;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0 0 0 1px #C6C6C6;
  cursor: pointer;
}

input[type=range]::-moz-range-thumb {
  -webkit-appearance: none;
  pointer-events: all;
  width: 24px;
  height: 24px;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0 0 0 1px #C6C6C6;
  cursor: pointer;
}

input[type=range]::-webkit-slider-thumb:hover {
  background: var(--orange-color);
}

input[type=range]::-webkit-slider-thumb:active {
  box-shadow: inset 0 0 3px #387bbe, 0 0 9px #387bbe;
  -webkit-box-shadow: inset 0 0 3px #387bbe, 0 0 9px #387bbe;
}

input[type="number"] {
  color: #8a8383;
  width: 60px;
  height: 30px;
  font-size: 20px;
  border: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  display: none;
}

input[type="range"] {
  -webkit-appearance: none;
  appearance: none;
  height: 2px;
  width: 100%;
  background-color: #C6C6C6;
  pointer-events: none;
}

#fromSlider {
  height: 0;
  z-index: 1;
  position: relative;
  top: 18px;
}

.products-list-wrapper {
  display: flex;
  flex-direction: column;
  padding: 0px 20px;
}

.products-list-header {
  display: flex;
  justify-content: space-between;
}


.sort-list {
  font-family: JetBrains Mono, monospace;
  display: block;
  height: 40px;
  background-color: var(--main-info-color);
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
  padding: 10px 30px;
  text-align: center;
  appearance: none;
}

.sort-option {
  margin: 10px;
}

.found {
  font-size: var(--main-info-font-size);
  margin: auto 0;
}

.search {
  height: 40px;
  font-size: var(--main-info-font-size);
  text-align: center;
  border: 2px solid var(--font-color);
}

::placeholder {
  font-family: JetBrains Mono, monospace;
}

.layout-img {
  width: 40px;
  cursor: pointer;
}

.products {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 20px;
  gap: 1rem;
}

.product-card {
  flex-direction: row;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  gap: 30px;
  padding: 20px;
  cursor: pointer;
  /* background-color: #5d8ebf; */
}

.product-card:hover {
  background-color: rgba(255, 166, 0, 0.2);
}

.product-img {
  width: 270px;
  margin: auto;
}

.product-title {
  font-size: var(--main-info-font-size);
  font-weight: bold;
}

.product-rating {
  color: var(--grey-color);
  margin: 10px;
}

.product-price {
  font-size: var(--title-font-size);
  font-weight: bold;
}

.product-button {
  font-family: JetBrains Mono, monospace;
  display: block;
  height: 40px;
  background-color: var(--main-info-color);
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
  padding: 0px 20px;
}

.product-button:hover {
  transition: 0.5s;
  background-color: var(--main-info-color-hover);
}

.product-button:active {
  transform: scale(0.90);
}

#buy-now {
  background-color: var(--orange-color);
}
